<template>
  <v-container>
    <div>
      <v-card tile class="mx-auto">
        <v-img
          position="bottom"
          class="white--text"
          height="70px"
          src="../assets/card-bg.jpg"
        >
          <v-card-text class="text-right text-h6 font-weight-black">
            PROFILE
          </v-card-text>
          <!-- <v-card-title>Top 10 Australian beaches</v-card-title> -->
        </v-img>
      </v-card>
      <v-row class="mt-2">
        <v-col cols="12" md="12">
          <v-card class="mx-auto">
            <div class="pa-10">
              <h3>
                <strong>{{ currentUser.username }}</strong>
              </h3>
              <p>
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Atque,
                consequatur esse. Sint distinctio eveniet rem facere incidunt
                mollitia tenetur accusamus iusto, in dicta adipisci saepe at,
                consequuntur, quam nulla nemo!
              </p>
              <p>
                <strong>Token:</strong>
                {{ currentUser.accessToken.substring(0, 20) }} ...
                {{
                  currentUser.accessToken.substr(
                    currentUser.accessToken.length - 20
                  )
                }}
              </p>
              <p>
                <strong>Id:</strong>
                {{ currentUser.id }}
              </p>
              <p>
                <strong>Email:</strong>
                {{ currentUser.email }}
              </p>
              <strong>Authorities:</strong>
              <ul>
                <li v-for="(role, index) in currentUser.roles" :key="index">
                  {{ role }}
                </li>
              </ul>

              <strong>Branch:</strong>
              <ul>
                <li>
                  {{ currentUser.branch }}
                </li>
              </ul>

              <strong>Department:</strong>
              <ul>
                <li>
                  {{ currentUser.dept }}
                </li>
              </ul>
            </div>
          </v-card>
        </v-col>
      </v-row>
    </div>
  </v-container>
</template>

<script>
export default {
  name: "Profile",
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    },
  },
  mounted() {
    if (!this.currentUser) {
      this.$router.push("/login");
    }
  },
};
</script>
